* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
}

.navbar-bg {
  background: #6e3cbc !important;
}

.navbar-brand {
  font-size: 2rem;
  background: #7267cb;
  padding: 10px 30px;
  border-radius: 50px;
}
.navbar-brand:hover {
  box-shadow: 1px 2px 4px 0px rgba(0, 0, 0, 0.459);
}

.box {
  max-width: 800px;
  border-radius: 50px;
  height: 55vh;
  background: #fff;
  box-shadow: 1px 0px 4px 0px rgba(0, 0, 0, 0.459);
}

.header {
  padding-bottom: 100px !important;
  background: #6e3cbc;
  border-radius: 0 0 50% 50%;
  box-shadow: 1px 2px 4px 0px rgba(0, 0, 0, 0.459);
}

/* .box-d {
    height: 50vh;
} */

.heading {
  margin-top: 10px;
  align-items: center;
  color: #7267cb;
  text-shadow: #655ab9;
}

.url {
  max-width: 400px;
  border-radius: 50px;
  margin: 30px;
}

.button {
  background: #7267cb;
  padding: 10px 20px;
  border-radius: 50px;
  color: #fff;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.459);
  border: none;
  text-decoration: none;
}
.button:hover {
  background: #655ab9;
  padding: 10px 20px;
  border-radius: 50px;
  color: rgba(255, 255, 255, 0.945);
}

input {
  border: 1px solid #655ab9 !important;
}

.link-box {
  padding: 10px;
  border-radius: 10px;
  border: 1px solid #655ab931;
}
